.home{
    display: flex;
    flex-direction: row;
}

.sideImg{
    flex: 1;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.businessImg{
    height: 35vh;
}

.sideDesc{
    flex: 1;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.2rem;
    text-align: center;
}

@media only screen and (max-width: 600px) {
    .home {
        flex-direction: column;
        height: 100vh;
    }

    .businessImg{
        width: 80%;
    }

    .sideDesc{
        width: auto;
        height: auto;
        margin: 20px;
    }
  }