.about{
    display: flex;
    flex-direction: row;
}

.sideImg{
    flex: 1;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.businessImg{
    height: 35vh;
}

.sideDesc{
    flex: 1;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 0 40px;
}

@media only screen and (max-width: 600px) {
    .about {
        flex-direction: column;
        height: 100vh;
    }

    .businessImg{
        width: 80%;
    }

    .sideDesc{
        margin: auto 10px;
        margin-top: -40px;
    }
  }